<template>
<div id="baseParent" class="top mainArea" style="text-align:center">
  <div class="sp_line_ss" />
  <div v-show="!isHowto" style="text-align:left">
    <img :src="this.$root.IMG_PATH + 'icon_howto.png'" style="width:20px"><span class="link" v-on:click="pushHowto">『まなんだー』とは？</span>
  </div>

  <div v-show="isHowto" class="howtoParent" style="width:100%; text-align:left; pointer:cursor" v-on:click="pushHowto">
    <span class="font_l font_b">『まなんだー』とは？</span>
    <div class="sp_line_s" />
    高校大学受験や資格取得の勉強、学校の課題などをするときのタイマーとしてご利用ください。<br>
    開始と終了時にTwitterでツイートすることで画像付きでフォロワーに報告することができます。<br>
    また、画像として保存することで、他のSNSへの添付や勉強の記録として残すこともできます。<br>
  </div>

  <div class="sp_line_xl" />
  <div>
    <!-- <img :src="this.$root.IMG_PATH + 'bg_timer.png'" style="width:100%; max-width:600px; margin:auto"> -->
    <span class="timerParent" style="font-family: 'M PLUS Rounded 1c';" :style="timerFontSize">{{timerLabel}}</span>
  </div>
  <div class="sp_line_xl" />
  <div>
    <div v-show="status == 'INIT'">
      <span class="font_xl">ボタンをおして勉強を開始してください。</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_start.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" style="width:150px" v-on:click="pushStatus('START')">勉強を開始する</button>
    </div>
    <div v-show="status == 'START'">
      <span class="font_xl">お勉強がんばってください！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_play.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('PAUSE')">休憩する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="status == 'PAUSE'">
      <span class="font_xl">休憩もたいせつです！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_pause.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESUME')">再開する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="status == 'STOP'">
      <span class="font_xl">おつかれさまでした！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_stop.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESET')">リセットする</button>
    </div>
    
    <div class="sp_line_xl" />
    
    <div v-show="status != 'INIT'" class="cardParent" style="width:95%; max-width:650px; margin:auto;">
      <img :src="this.$root.IMG_PATH + labelArray[0]" style="width:70%; margin:auto">
      <div class="sp_line_s" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <span class="stepNo font_xl">1</span><span class="font_b font_xl">タイトルを設定しましょう</span>
      <div class="sp_line_m" />
      <input type="text" class="form-control inputBase" maxlength="50" style="width:70%; max-width:600px; margin:auto" placeholder="例）〇〇大学受験、学校の課題、〇〇資格取得"
      v-model="titleName" @change="chTitle">
      <div class="sp_line_s" />
      {{labelArray[1]}}

      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <span class="stepNo font_xl">2</span><span class="font_b font_xl">フレームを選択しましょう</span>
      <div class="sp_line_m" />
      選択中：{{frameNameList[selectedFrame]}}
      <button class="btnBase" v-on:click="pushBtn('FRAME')">フレーム変更</button>
      <div v-show="isSelectFrame">
        <div class="sp_line_m" />
        <div style="display:flex; gap:10px; flex-wrap:wrap; width:80%; margin:auto">
          <div v-for="(val, index) in frameList" :key="index">
            <img :src="this.$root.IMG_PATH + 'frame/frame_demo_' + index + '.png'" style="width:100px" :class="selFrame(index)" v-on:click="chFrame(index)" class="link">
          </div>
        </div>
      </div>

      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <div v-show="status == 'STOP'">
        <span class="stepNo font_xl">3</span><span class="font_b font_xl">時間の修正をしましょう</span>
        <div class="sp_line_m" />
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cHour">
        <span style="margin:0 10px">時間</span>
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cMinute">
        <span style="margin:0 10px">分</span>
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cSecond">
        <span style="margin-left:10px">秒</span>

        <div class="sp_line_l" />
        <div class="dotBar" />
        <div class="sp_line_m" />

      </div>

      <span class="font_b font_xl">デザインを確認してください</span>
      <div class="sp_line_m" />
      <div :style="'width:95%; height:' + (314 * cardBaseScale) + 'px; margin:auto;'" class="cardFC">
        <div id="cardBase" :style="'border: 1px solid rgba(88,88,88,0.2); position:relative; width:600px; margin:auto; font-family: \'Zen Maru Gothic\', \'Kosugi Maru\'; transform-origin: top left; transform : scale(' + cardBaseScale + ')'">
          <img :src="this.$root.IMG_PATH + 'frame/card_base_' + selectedFrame + '.png'" style="width:100%">

          <span id="cardTitle" style="position:absolute; top:30%; left:50%; font-size:10px; line-height: 37px; transform: translate(-50%, -50%) !important; -webkit-transform: translate(-50%, -50%) !important; -ms-transform: translate(-50%, -50%) !important;" :style="cardFont(0)">{{titleName}}</span>

          <span style="position:absolute; top:55%; left:50%; font-size:10px; white-space: nowrap; transform: translate(-50%, -50%) !important; -webkit-transform: translate(-50%, -50%) !important; -ms-transform: translate(-50%, -50%) !important;" :style="cardFont(1)">{{labelArray[1]}}</span>

          <span v-show="this.status == 'STOP'" style="position:absolute; bottom:12%; right:5%; white-space: nowrap; font-size:20px; transform: translateY(-50%) !important; -webkit-transform: translateY(-50%) !important; -ms-transform: translateY(-50%) !important;">{{cardTimeLabel}}</span>
        </div>
      </div>
      
      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <span v-show="errMsg != ''" class="errMsg">{{errMsg}}</span>
      <button v-show="status != 'STOP'" class="btnBase" v-on:click="pushBtn('TWEET_START')">ツイートする</button>
      <button v-show="status == 'STOP'" class="btnBase" v-on:click="pushBtn('TWEET_END')">ツイートする</button>
      <button class="btnBase" v-on:click="pushBtn('DOWNLOAD')">DOWNLOAD</button>
      
      <div class="sp_line_l" />
      <p class="font_m" style="text-align:left; width:90%; margin:auto">
        * TwitterにURL付きでツイートすると、上でデザインした画像が表示されます。<br>
        * その他のSNSへ投稿する場合や、画像として保存する場合はDOWNLOADボタンを押してください。
      </p>
    </div>
  </div>

  <div class="sp_line_l" />
</div>
<popup-window ref="popup" @pushOpenLink="pushOpenLink"></popup-window>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      status : "INIT",
      
      startTime : null,
      timerValue : 0,
      currentTime : 0,

      titleName : "",
      cHour : "",
      cMinute : "",
      cSecond : "",

      frameList : ["0","1","2","3","4","5"],
      frameNameList : ["フレーム0","フレーム1","フレーム2","フレーム3","フレーム4","フレーム5"],
      selectedFrame : 0,
      isSelectFrame : false,

      labelArray : ["", "の勉強を始めました"],
      labelArray1 : ["img_make_start.png", "の勉強を始めました"],
      labelArray2 : ["img_make_stop.png", "の勉強を終了しました"],

      baseParentWidth : 1000,
      cardBaseScale : 1.0,

      isHowto : false,

      errMsg : "",
    }
  },
  created : function () {
    console.log(parseInt("ああ"));
  },
  computed : {
    timerFontSize : function () {
      let fSize = this.baseParentWidth / 6.5;
      if (fSize > 90) {
        fSize = 90;
      }
      return "font-size:" + fSize + "px";
    },
    timerLabel : function () {
      var hours = Math.floor(this.timerValue / 3600);
      var minutes = Math.floor((this.timerValue % 3600) / 60);
      var remainingSeconds = this.timerValue % 60;

      var formattedTime = '';
      formattedTime += hours.toString().padStart(2, '0') + ':';
      formattedTime += minutes.toString().padStart(2, '0') + ':';
      formattedTime += remainingSeconds.toString().padStart(2, '0');
      return formattedTime;
    },
    cardTimeLabel : function () {
      var formattedTime = '';
      if (this.cHour != "" && parseInt(this.cHour) != 0) {
        if (isNaN(parseInt(this.cHour))) {
          formattedTime += '--時間';
        } else {
          formattedTime += parseInt(this.cHour) + '時間';
        }
      }
      if (this.cMinute != "" && parseInt(this.cMinute) != 0) {
        if (isNaN(parseInt(this.cMinute))) {
          formattedTime += '--分';
        } else {
          formattedTime += parseInt(this.cMinute) + '分';
        }
      }
      if (this.cSecond != "" && parseInt(this.cSecond) != 0) {
        if (isNaN(parseInt(this.cSecond))) {
          formattedTime += '--秒';
        } else {
          formattedTime += parseInt(this.cSecond) + '秒';
        }
      }
      return formattedTime;
    },
    selFrame : function () {
      return (index) => {
        if (index == this.selectedFrame) {
          return "selectFrame";
        } else {
          return "";
        }
      }
    },
    cardFont : function () {
      return (kbn) => {
        let cardWidth = 600;
        if (kbn == 0) {
          //タイトル
          let tLength = 0;
          for (let i = 0; i < this.titleName.length; i++) {
            const charCode = this.titleName.charCodeAt(i);
            if ((charCode >= 0x0020 && charCode <= 0x007E) || (charCode >= 0xFF61 && charCode <= 0xFF9F)) {
              tLength += 1; // 半角文字
            } else {
              tLength += 2; // 全角文字
            }
          }

          let fSize = 40;
          if (fSize > (cardWidth * 0.75 / tLength) * 2) {
            fSize = (cardWidth * 0.75 / tLength) * 2;
            if (fSize < 25) {
              //2行にする
              return "font-size: 25px; white-space: normal; width: " + (cardWidth * 0.75) + "px";
            }
          }
          
          return "font-size:" + fSize + "px; white-space: nowrap;";
        } else if (kbn == 1) {
          //ラベル
          return "font-size:" + "20" + "px";
        }
      }
    },
  },
  mounted () {
    this.$nextTick(function() {
      //タイトル
      this.titleName = localStorage.getItem("TITLE_NAME") || "";
    });

    this.baseParentWidth = document.getElementById('baseParent').clientWidth;
    this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
    if (this.cardBaseScale > 1) {
      this.cardBaseScale = 1.0;
    }
    window.addEventListener('resize', this.calculateWindowWidth);
  },
  methods : {
    pushHowto : function () {
      this.isHowto = !this.isHowto;
    },
    calculateWindowWidth : function () {
      this.baseParentWidth = document.getElementById("baseParent").clientWidth;
      this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
      if (this.cardBaseScale > 1) {
        this.cardBaseScale = 1.0;
      }
    },
    pushBtn : function (name) {
      if (name == "FRAME") {
        this.isSelectFrame = !this.isSelectFrame;
      } else if (name == "TWEET_START" || name == "TWEET_END") {
        this.errMsg = "";

        //入力チェック
        if (this.titleName == "") {
          this.errMsg = "タイトルを入力してください";
          return;
        } else if (this.titleName.length > 50) {
          this.errMsg = "タイトルは50文字以下で入力してください";
          return;
        }

        let kbn = 0;
        if (name == "TWEET_END") {
          if (this.cHour == "") {
            this.cHour = "0";
          }
          if (this.cMinute == "") {
            this.cMinute = "0";
          }
          if (this.cSecond == "") {
            this.cSecond = "0";
          }
          
          if (parseInt(this.cHour) == 0 && parseInt(this.cMinute) == 0 && parseInt(this.cSecond) == 0) {
            this.errMsg = "勉強していません";
            return;
          }
          if (isNaN(parseInt(this.cHour)) || parseInt(this.cHour) >= 0 || isNaN(parseInt(this.cMinute)) || parseInt(this.cMinute) >= 0 || isNaN(parseInt(this.cSecond)) || parseInt(this.cSecond) >= 0) {
            this.errMsg = "正しい時間を入力してください";
            return;
          }
          kbn = 1;
        }

        //現在の情報をDBに保存
        let params = new URLSearchParams();
        params.append('titleName', this.titleName);
        params.append('frameIndex', this.selectedFrame);
        params.append('kbn', kbn);

        if (name == "TWEET_END") {
          let studyTime = parseInt(this.cHour) * 60 * 60 + parseInt(this.cMinute) * 60 + parseInt(this.cSecond);
          params.append("studyTime", studyTime);
        }

        axios.post(this.$root.SERVER_PATH + "reportStudy", params)
        .then((response) => {
          let resData = response.data;

          console.log(resData);

          if (resData["status"] == this.$root.API_SUCCESS) {
            //成功
            let link = resData["code"];
            let text = this.titleName;

            if (name == "TWEET_START") {
              text = text + "の勉強をはじめました。";
            } else {
              let timeStr = "";
              if (parseInt(this.cHour) != 0) {
                timeStr = timeStr + this.cHour + "時間";
              }
              if (parseInt(this.cMinute) != 0) {
                timeStr = timeStr + this.cMinute + "分";
              }
              if (parseInt(this.cSecond) != 0) {
                timeStr = timeStr + this.cSecond + "秒";
              }
              text = text + "の勉強を終了しました。 勉強時間：" + timeStr;
            }

            var url = "https://twitter.com/intent/tweet?" + 
            "hashtags=" + encodeURIComponent('まなんだったー') + 
            "&text=" + encodeURIComponent(text) + 
            "&url=" + encodeURIComponent(this.$root.BASE_URL + link);
            window.open(url);
          } 
          // if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          //   this.$refs.popup.errorMessage = "暗証番号が正しくありません";
          // } else if (resData["status"] == this.$root.API_SUCCESS) {
          //   this.$refs.popup.title = "";
          //   this.$refs.popup.body = "";
          //   this.$refs.popup.showModal("DELETE_SUCCESS");
          // }
          //  else if (resData["status"] == this.$root.API_SUCCESS) {
          // }
        });
      } else if (name == "DOWNLOAD") {
        //画像化
        window.html2canvas(document.getElementById('cardBase'), { 
          scale:1
        }).then(canvas => {
          // var base64 = canvas.toDataURL('image/jpeg');
          // base64 = base64.replace(/^.*,/, '');

          const a = document.createElement("a");
          a.href = canvas.toDataURL("image/jpeg", 1);
          a.download = "study_timer.png";
          a.click();

          // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
        });
      }
    },
    chFrame : function (index) {
      this.selectedFrame = index;
      this.isSelectFrame = false;
      localStorage.setItem("SEL_FRAME", this.selectedFrame);
    },
    chTitle : function () {
      localStorage.setItem("TITLE_NAME", this.titleName);
    },
    pushStatus : function (val) {
      this.status = val;
      this.labelArray = this.labelArray1;

      if (val == "RESET") {
        //リセット
        this.timerValue = 0;
        this.currentTime = 0;
        this.startTime = "";
        this.status = "INIT";
        localStorage.removeItem("START_TIME");
        localStorage.removeItem("CURRENT_SEC");
      } else if (val == "START") {
        //開始
        this.calculateWindowWidth();
        this.startTime = new Date();
        this.currentTime = 0;
        this.timerValue = 0;
        localStorage.setItem("START_TIME", this.startTime.toLocaleString());
        localStorage.setItem("CURRENT_SEC", 0);
      } else if (val == "STOP") {
        //終了
        this.currentTime = this.timerValue;
        this.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.currentTime);
        localStorage.removeItem("START_TIME");
        this.labelArray = this.labelArray2;

        this.cHour = Math.floor(this.currentTime / 3600);
        this.cMinute = Math.floor((this.currentTime % 3600) / 60);
        this.cSecond = this.currentTime % 60;
      } else if (val == "PAUSE") {
        //一時停止
        //これまでの秒数を保管
        let addVal = Math.floor((new Date() - this.startTime) / 1000);
        this.currentTime += addVal;
        this.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.currentTime);
        localStorage.removeItem("START_TIME");
      } else if (val == "RESUME") {
        //再開
        this.status = "START";
        this.startTime = new Date();
        localStorage.setItem("START_TIME", this.startTime.toLocaleString());
      }
      localStorage.setItem("STATUS", this.status);
    },
    initAction : function () {
      //前回までの状態を取得
      this.status = localStorage.getItem("STATUS") || "INIT";
      if (this.status == "STOP") {
        this.labelArray = this.labelArray2;
      } else {
        this.labelArray = this.labelArray1;
      }

      let cTime = localStorage.getItem("CURRENT_SEC");
      if (cTime == null) {
        this.currentTime = 0;
      } else {
        this.currentTime = parseInt(cTime);
      }

      let startStr = localStorage.getItem("START_TIME");
      if (startStr != null) {
        this.startTime = new Date(startStr);
      } else {
        this.startTime = null;
      }

      if (this.status == "STOP" || this.status == "MAKE") {
        this.cHour = Math.floor(this.currentTime / 3600);
        this.cMinute = Math.floor((this.currentTime % 3600) / 60);
        this.cSecond = this.currentTime % 60;
      }
      console.log(this.status + " " + this.currentTime + " " + this.startTime);
      
      if (this.startTime != null) {
        this.timerValue = Math.floor((new Date() - this.startTime) / 1000) + this.currentTime;
      } else {
        this.timerValue = this.currentTime;
      }
      
      //フレーム
      this.selectedFrame = localStorage.getItem("SEL_FRAME") || 0;

      const countUp = function(){
        if (this.status == "START") {
          let val = 0;
          if (this.startTime != null) {
            val = Math.floor((new Date() - this.startTime) / 1000);
          }
          this.timerValue = val + this.currentTime;
        }
      }.bind(this);
      setInterval(countUp, 1000);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

<style>
.loginFrameBase {
	width: 80%;
	margin: 0 auto;
	text-align: center;
	color: #5c87a6;
}

a.loginFrame {
  display: block;
	padding: 20px;
	text-decoration: none;
	color: #333;
	border-radius: 10px;
	border: solid 3px #ccc;
	background-color: #fff;
}

a.loginFrame:hover {
  background-color: #fffdc5;
	text-decoration: none;
}

</style>